import React, { Component } from "react";
import { Formik, Field, Form, ErrorMessage } from "formik";
import * as yup from "yup";
import s from "./FormLogin.module.css";
import firebase from "firebase";
import "firebase/auth";
import "firebase/firestore";

export default class FormRegistr extends Component {
    createAccount(email, password, lastName) {
   
        firebase.auth().createUserWithEmailAndPassword(email, password)
        .catch(error => console.log(error));

        alert(`,${lastName},      .      `)
       
        this.props.showRegistrModal();
       
}

  render() {
    const phoneRegExp = RegExp(
      /^\(?([0-9]{3})\)?[-. ]?([0-9]{3})[-. ]?([0-9]{4})$/
    );

    return (
      <div onClick={this.props.showRegistrModal} className={s.formLogin}>
        <Formik
          initialValues={{
            firstName: "",
            lastName: "",
            phoneNumber: "",
            email: "",
            password: "",
            confirmPassword: "",
            checkbox: false,
          }}
          validationSchema={yup.object().shape({
            firstName: yup.string().required(" "),
            lastName: yup.string().required(" "),

            email: yup
              .string()
              .email("    ")
              .required(" "),
            phoneNumber: yup
              .string()
              .matches(phoneRegExp, "   ")
              .required(" "),
            password: yup
              .string()
              .min(6, "     6 ")
              .required(" "),
            confirmPassword: yup
              .string()
              .oneOf([yup.ref("password"), null], "  "),

            checkbox: yup.boolean().oneOf([true], "     "),
          })}
          onSubmit={(values) => {
              console.log(values);
              const { email, password, lastName } = values;
              this.createAccount(email, password, lastName);
          }}
        >
          <Form onClick={(e) => e.stopPropagation()} className={s.form}>
            <h1 className={s.formTitle}></h1>
            <Field
              name="firstName"
              type="text"
              placeholder="  "
              className={s.formInput}
            />
            <ErrorMessage
              name="firstName"
              component="div"
              className={s.error}
            />

            <Field
              name="lastName"
              type="text"
              placeholder="  "
              className={s.formInput}
            />
            <ErrorMessage name="lastName" component="div" className={s.error} />
            <Field
              name="email"
              type="email"
              placeholder="   "
              className={s.formInput}
            />
            <ErrorMessage name="email" component="div" className={s.error} />

            <Field
              name="phoneNumber"
              type="text"
              placeholder="    - 0680573809"
              className={s.formInput}
            />
            <ErrorMessage
              name="phoneNumber"
              component="div"
              className={s.error}
            />

            <Field
              name="password"
              type="password"
              placeholder="  "
              className={s.formInput}
            />
            <ErrorMessage name="password" component="div" className={s.error} />

            <Field
              name="confirmPassword"
              type="password"
              placeholder="  "
              className={s.formInput}
            />
            <ErrorMessage
              name="confirmPassword"
              component="div"
              className={s.error}
            />

            <div class={s.checkbox}>
              <Field
                name="checkbox"
                type="checkbox"
                id="checkbox"
                className={s.formCheckbox}
              />
              <label htmlFor="checkbox">   </label>
              <ErrorMessage
                name="checkbox"
                component="div"
                className={s.error}
              />
            </div>
            <button type="submit" className={s.btnSign}>
              <span className={s.btnTitle}></span>
            </button>
          </Form>
        </Formik>
        <div className={s.btnCloseLogin}>
          <span>X</span>
        </div>
      </div>
    );
  }
}
